body {
    background-color: rgb(21 24 29);
    color: #eee;
}

input :focus {
    outline: none;
    border: solid 0.2px;
}

.App {
    text-align: center;
    width: 100%;
}

.mainLogo {
    font-size: 70px;
    font-weight: bold;
    width: 700px;
    margin: 25px auto 50px;
}

.searchBarBox {
    margin: 0 auto;
    width: 700px;
    position: relative;
}
.searchBar {
    border: solid 0.2px;
    border-radius: 10px;
    border-color: #707f96;
    height: 35px;
    margin-right: 15px;
    width: 100%;
    background-color: #343a46;
    color: #eee;

    text-align: center;
}

.searchBotton {
    position: absolute;
    right: 2px;
    top: 4px;
    cursor: pointer;
    background-color: rgb(21 24 29);
    color: #eee;
    width: 50px;

    border: solid 1px;
    border-radius: 15px;
    border-color: #2b313a;
}
.chaBox {
    background-color: #1c1f26;

    width: 700px;
    margin: 30px auto;
    padding: 30px 0;
    box-shadow: 4px 6px 11px -5px #000000;

    border: solid 1px;
    border-radius: 12px;
    border-color: #2b313a;
}

.nameof {
    font-weight: bold;
    font-size: 2rem;
}

.chaImg {
    width: 300px;
    border: solid 1px;
    border-radius: 15px;
    border-color: #2b313a;
    margin-top: 5px;
}

.oneJNume {
    font-weight: bold;
}

.equBox {
    background-color: #1c1f26;

    margin: 50px 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    box-shadow: 4px 6px 11px -5px #000000;

    width: 700px;
    height: 330px;
    margin: 0 auto 1000px;
    justify-content: center;
    align-items: center;
    border: solid 1px;
    border-radius: 12px;
    border-color: #2b313a;
}

.itemBoxSmall {
    display: flex;
    height: 45px;
    width: 182px;
    margin-bottom: 5px;
    text-align: left;
    position: relative;
}

.itemBoxSmall:nth-child(n + 13) {
    display: none;
}

.itemBoxSmall > span,
.itemBoxSmall > img {
    display: block;

    box-shadow: 4px 6px 11px -5px #000000;

    width: 50px;
    height: 45px;
    border: 1px;
    border-color: #2b313a;
}
.itemBoxSmall > img {
    border: solid 1px;
    border-radius: 5px;
    border-color: #2b313a;
    justify-content: flex-start;
    background-color: rgb(21 24 29);
    display: inline-block;
    margin-right: 7px;
}

.itemBoxSmall:nth-child(n + 7) {
    width: 150px;
}

.itemLevel {
    border: solid 1px;
    border-radius: 5px;
    border-color: #2b313a;

    width: 42px;
    display: inline-block;
    text-align: center;
    margin-left: 5px;

    box-shadow: 4px 6px 11px -5px #000000;
}

.purple {
    background-color: purple;
    width: 30px;
    display: inline-block;
    text-align: center;
    border-radius: 7px;
    right: 0;
    position: absolute;
}

.blue {
    background-color: blue;
    width: 30px;
    display: inline-block;
    text-align: center;
    border-radius: 7px;
    right: 0;
    position: absolute;
}

.green {
    background-color: green;
    width: 30px;
    display: inline-block;
    text-align: center;
    border-radius: 7px;
    right: 0;
    position: absolute;
}

.yellow {
    background-color: rgb(177, 177, 0);
    width: 30px;
    display: inline-block;
    text-align: center;
    border-radius: 7px;
    right: 0;
    position: absolute;
}

.orange {
    background-color: rgb(228, 137, 0);
    width: 30px;
    display: inline-block;
    text-align: center;
    border-radius: 7px;
    right: 0;
    position: absolute;
}

@media all and (max-width: 768px) {
    body {
        font-size: 0.9rem;
    }
    .mainLogo {
        width: 420px;
    }
    .searchBarBox {
        width: 420px;
    }
    .chaBox {
        width: 420px;
    }

    .equBox {
        width: 420px;
    }
    .itemBoxSmall {
        width: 170px;
    }
}
